<template>
	<view class="content">
		<!-- 自定义导航栏 -->
		<navbar></navbar>
		<!--自定义选项卡-->
		<tab :list="tabList"></tab>
	</view>
</template>

<script>
	// easyCom   components/组件名/组件名.vue 局部引入
	// import navbar from '@/components/navbar/navbar.vue'
	export default {
		// components:{
		// 	navbar
		// },
		data() {
			return {
				tabList:[]
			}
		},
		onLoad() {
			this.getLabel()
		},
		methods: {
			getLabel() {
				this.$api.get_list().then(res=>{
					console.log(res)
				})
				this.$api.get_label({
						name: 'get_label',
				}).then(res=>{
						const{data} =res//里面没有result了 只有data
						this.tabList=data
						console.log(this.tabList)
				})
			}
		}
	}
</script>

